import React, { Component } from 'react';
import MyTools from './MyTools'
import Language from './Language'

export class Parking extends Component {
  props: any;
  static setWidthDone = false;  

  componentDidMount() {
    if (!Parking.setWidthDone)
    {
      Parking.setWidthDone = true;
      let maxWidth = 0;
      const actionClass = document.getElementsByClassName("Action") as HTMLCollectionOf<HTMLElement>;
      Array.from(actionClass).forEach(
        r => maxWidth = Math.max(maxWidth, r.clientWidth));

      Array.from(actionClass).forEach(r => r.style.width = String(maxWidth+10)+"px");
      
      console.log("Action Width=" + maxWidth);
    }
  }

  render() {
    return (
      <div className={MyTools.GetDivClassName(this.props.action, this.props.isLight)}>
        <div className="ib1">
          <div className="flexAddress">
            <div className="Action">
              <h1>
              <a href={MyTools.GetUrl(this.props.action, this.props.startDate, this.props.endDate, this.props.carid)}>{Language.t(this.props.action)}</a>
              </h1>
            </div>
            <div>
              {this.DrawStartAddress()}
            </div>
            <div>
              <h4>{this.props.endAddress}</h4>
            </div>
          </div>
          <div  className="flexColumns">
            <div >
              {Language.t("Datum")}: {MyTools.FormatUnixTimestamp(this.props.startDate)}
              <br />
              {MyTools.DrawLabelIfnotNull(Language.t("Dauer") + ": ", this.props.minuteDiff, " " + Language.t("Minuten"))}
              <br />
              {Language.t("km Stand")}: {Math.round(this.props.odometer * 10) / 10} km
            </div>
            <div >
              {Language.t("Verbrauch")}: {Math.round(this.props.kWh * 10) / 10}kWh
              <br />
              {Language.t("kmDiff")}: {Math.round(this.props.rangeDiff * 10) / 10}km
              <br />
              {Language.t("SOC")}: {this.props.soc}% / {Language.t("SOC diff")}: {this.props.socDiff}%
            </div>
            <div >{MyTools.DrawLabelIfnotNullWithRound(Language.t("Ø Temperatur")+": ", this.props.outsideTempAvg, '°C', 10)}</div>
          </div>
        </div>
        <div className="ib2">
          <a href={MyTools.GetUrl(this.props.action, this.props.startDate, this.props.endDate, this.props.carid)}>
            <img src={this.GetMapImg()} alt=""/>
          </a>
        </div>
      </div>
    );
  }
  GetMapImg()
  {
    if (this.props.action === "Charging")
      return "public/plugins/teslalogger-timeline-panel/maps/C-" + this.props.imgname+".jpg";
    else if (this.props.action === "Driving")
      return "public/plugins/teslalogger-timeline-panel/maps/T" + this.props.carid+"-" + this.props.startposid + "-" + this.props.endposid+".jpg";
    else if (this.props.action === "Parking")
      return "public/plugins/teslalogger-timeline-panel/maps/P-" + this.props.imgname+".jpg";
    
    return "";
  }


  DrawStartAddress()
  {
    if (this.props.endAddress.length > 0)
    {
      return <h4>{this.props.startAddress} ➔</h4>;
    }
    return <h4>{this.props.startAddress}</h4>;
  }
}
